.App {
  text-align: center;
}

.App-logos {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.App-logo {
  display: inline-block;
  height: 30vmin;
  margin: 12px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
